import React from 'react';
import Navbar from './Navbar';
import Search from './Search';
import Cards from './Cards';
import Graph from './Graph';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          query: '',
          input: '',
          filters: [],
          responseNumber: 0,
          credits: sessionStorage.getItem('credits'),
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleFilters = this.handleFilters.bind(this);
        this.handleNumberOfResponses = this.handleNumberOfResponses.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
    handleInputChange = (event) => {
        this.setState({
            input: event.target.value,
        })
    }
    
    handleSearch = () => {
        this.setState({
          query: this.state.input,
        })
    }

    handleFilters = (event, {value}) => {
        this.setState({
            filters: value,
        })
    }

    handleNumberOfResponses = (event, {value}) => {
        this.setState({
            responseNumber: value,
        })
    }

    handleSubmit = () => {
    //   console.log('Submitted')
      this.setState({
        query: '',
        credits: Number(this.state.credits)+localStorage.length
      })
      var credits= Number(this.state.credits)+localStorage.length
      sessionStorage.setItem('credits', credits)
      localStorage.clear();
    }

    render() {
        return (
            <div>
                <Navbar credits={this.state.credits} />
                <div style={{marginTop: '30px'}}>
                    <Search handleInputChange={this.handleInputChange} handleSearch={this.handleSearch} handleFilters={this.handleFilters} handleNumberOfResponses={this.handleNumberOfResponses} />
                    {(this.state.query=='')?'':<Cards query={this.state.query} filters={this.state.filters} responseNumber={this.state.responseNumber} handleSubmit={this.handleSubmit}/>}
                    <Graph />
                </div>
            </div>
        );
    }
}

export default App;
