import React from 'react';
import { Image, Menu, Dropdown, Icon } from 'semantic-ui-react';


class Navbar extends React.Component {

  render() {  
    const trigger = (
      <span>
        <Image avatar src={sessionStorage.getItem("userProfileImage")} avatar />
      </span>
) 
    return (
        <Menu inverted>
          <Menu.Item >
            <Image size="mini" 
              src='https://clipart.info/images/ccovers/1495750444Gold-Coins-PNG-Clipart.png' 
              verticalAlign='middle' />
            <span>{(this.props.credits==null)?0:this.props.credits}</span>
          </Menu.Item>
          <Menu.Item position='right'>
            <Dropdown trigger={trigger} pointing='top right' icon={null}>
              <Dropdown.Menu>
                <Dropdown.Header>{sessionStorage.getItem("userName")}</Dropdown.Header>
                <Dropdown.Item href={`/`}><Icon name='sign out' />Sign Out</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Menu.Item>
          
        </Menu>
    );
  }
}

export default Navbar;
